html, body{
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    background: #e9f7fc;
}
.graph{
    font: 15px sans-serif;
    font-weight: 600;
}
.linage {
    fill: none;
    stroke: #B0BEC5;
    stroke-width: 5;
}
.imageWrap {
    position: absolute;
    background-color: #fff;
    padding: 5px;
    border-radius: 100px;
    border: 5px solid;
}
.imageWrap img {
    border-radius: 100px;
}
.imageDetail {
    clear: both;
    z-index: 2;
    margin-top: 50px;
}
.imageDetail p {
    margin: 0;
    padding: 0;
}
.marriage {
    fill: none;
    stroke: #B0BEC5;
    stroke-width: 5;
    /*stroke-dasharray: 0.5%;*/
}
.male {
    box-sizing: border-box;
    border-color: blue !important;
}

.death {
    background-color: gray !important;
}
.alive {
    background-color: green !important;
}
.female {
    box-sizing: border-box;
    border-color: pink !important;
}

svg {
    border-style: solid;
    border-width: 1px;
}
.detail-column-name, .detail-column-value {
    padding: 5px;
    margin: 5px;
}

